
import { defineComponent, ref, provide, reactive } from "vue";
import { Files, filesPPPK } from "./cat-riwayat";
import { useRouter } from "vue-router";
import { gf as $gf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();
    const router = useRouter();
    const config = $gf.configGet();

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };
    const tryoutHasil = (data: any): void => {
      router.push({name: 'tryout-hasil', params: data});
    }

    const dataRiwayat = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    })

    const titleTable = [
      "Kode",
      "Type",
      "Total Skor",
      "Status",
      "Start Date",
      "End Date",
      "#"
    ];

    const getData = () => {
      cash('#loadingCR').show();

      const page = dataRiwayat.page;
      const pageSize = dataRiwayat.pageSize;
      axios
        .get(api.getHistoryCat + '?page=' + page + '&pageSize=' + pageSize, config)
        .then(async (result) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          console.log('response get hc ', rs);

          dataRiwayat.data = rs;
          dataRiwayat.paginate  = $gf.paginate(ds.totalData, page, pageSize, 3);
          console.log('dataRiwayat.paginate ', dataRiwayat.paginate);
          cash('#loadingCR').hide();
        })
        .catch((error) => {
          console.log("response get hc err ", error.response);
          $gf.handleJwtExpired(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    }

    const goToPage = (p: number) => {
      dataRiwayat.page = p;
      getData();
    }

    const nextPage = () => {
      dataRiwayat.page = dataRiwayat.page + 1;
      getData();
    }

    const beforePage = () => {
      dataRiwayat.page = dataRiwayat.page - 1;
      getData();
    }

    const lastPage = () => {
      dataRiwayat.page = dataRiwayat.paginate.totalPages;
      getData();
    }

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
      tryoutHasil,
      titleTable,
      dataRiwayat,
      router,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
    };
  },
  data() {
    return {
      filter: {
        field: "skd",
      }
    }
  },
  methods: {
    goDetail(data: any): void{
      this.router.push('riwayat-cat/' + data.type + '/hasil' + '?kode=' + data.kode);
    }
  }
});
