import { App } from "vue";

const host = "https://api.cpnsonline.id/";
const version = "v1/";
const url = host + version;
const apiConfig = {
  // auth
  login: url + "auth/login",
  getMenu: url + "auth/menu",

  // member
  getMateri: url + "member/materi/",
  getJenisTryout: url + "member/cat/jenis/",
  getWidgetTop: url + "member/widget",
  getSK: url + "member/sk-garansi",
  getTB: url + "member/tahap-belajar",
  getPassingGrade: url + "member/cat/passing-grade/",
  soalCat: url + "member/cat/question/",
  submitCat: url + "member/cat/submit/",
  getPembahasanCat: url + "member/cat/pembahasan/",
  getHasilCat: url + "member/cat/result/",
  getHistoryCat: url + "member/cat/history/",
  getPeringkatCat: url + "member/cat/leaderboard/",

  // admin
  getDataUser: url + "admin/user/",
};

const install = (app: App): void => {
  app.config.globalProperties.$gf = apiConfig;
};

export { install as default, apiConfig as api };
