import {
  createRouter,
  createWebHistory,
  RouteLocationNormalized,
  RouteRecordRaw,
} from "vue-router";
import { gf as $gf } from "../utils/global-functions";
import { VueCookieNext as $ck } from "vue-cookie-next";

import Home from "../views/home/Main.vue";

import SideMenu from "../layouts/side-menu/Main.vue";
// import SimpleMenu from "../layouts/simple-menu/Main.vue";
// import TopMenu from "../layouts/top-menu/Main.vue";
// import Dashboard from "../views/dashboard/Main.vue";
import DashboardMember from "../views/dashboard-member/Main.vue";
import DashboardAdmin from "../views/dashboard-admin/Main.vue";
// import Inbox from "../views/inbox/Main.vue";
import Login from "../views/login/Main.vue";
import Register from "../views/register/Main.vue";
import ErrorPage from "../views/error-page/Main.vue";
import InProgressPage from "../views/in-progress/Main.vue";
import MateriPenunjang from "../views/materi-penunjang/Main.vue";
import DetailMateri from "../views/materi-detail/Main.vue";
import TipsAndTrik from "../views/tips-and-trik/Main.vue";
import TryoutSkb from "../views/tryout-skb/Main.vue";
import TryoutSkd from "../views/tryout-skd/Main.vue";
import CatPppk from "../views/cat-pppk/Main.vue";
import CatCpns from "../views/cat-cpns/Main.vue";
import TryoutExam from "../views/tryout-exam/Main.vue";
import TryoutHasil from "../views/tryout-hasil/Main.vue";
import CatPeringkat from "../views/cat-peringkat/Main.vue";
import CatRiwayat from "../views/cat-riwayat/Main.vue";
import Tutorial from "../views/tutorial/Main.vue";
import KelasPrivate from "../views/kelas-private/Main.vue";
import KelasWebinar from "../views/kelas-webinar/Main.vue";
import ResponChat from "../views/respon-chat/Main.vue";
import FormKomplain from "../views/form-komplain/Main.vue";
import CekGaransi from "../views/cek-garansi/Main.vue";
import UpgradeAkun from "../views/upgrade-akun/Main.vue";
import DataUser from "../views/data-user/Main.vue";
import BonusMateri from "../views/bonus-materi/Main.vue";
import PaketSoal from "../views/paket-soal/Main.vue";
import BankSoal from "../views/bank-soal/Main.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "home",
    component: Home,
  },
  {
    path: "/member",
    component: SideMenu,
    children: [
      {
        path: "",
        name: "member-dashboard",
        component: DashboardMember,
      },
      {
        path: "materi-penunjang",
        name: "member-materi-penunjang",
        component: MateriPenunjang,
      },
      {
        path: "materi-penunjang/:slug",
        name: "member-detail-materi-penunjang",
        component: DetailMateri,
      },
      {
        path: "tips-and-trik",
        name: "member-tips-and-trik",
        component: TipsAndTrik,
      },
      {
        path: "tips-and-trik/:slug",
        name: "member-detail-tips-and-trik",
        component: DetailMateri,
      },
      {
        path: "cat-pppk",
        name: "member-cat-pppk",
        component: CatPppk,
      },
      {
        path: "cat-pppk/:slug",
        name: "member-cat-pppk-exam",
        component: TryoutExam,
      },
      {
        path: "cat-pppk/:slug/hasil",
        name: "member-cat-pppk-hasil",
        component: TryoutHasil,
      },
      {
        path: "cat-cpns",
        name: "member-cat-cpns",
        component: CatCpns,
      },
      {
        path: "cat-cpns/:slug",
        name: "member-cat-cpns-exam",
        component: TryoutExam,
      },
      {
        path: "cat-cpns/:slug/hasil",
        name: "member-cat-cpns-hasil",
        component: TryoutHasil,
      },
      {
        path: "riwayat-cat",
        name: "member-riwayat-cat",
        component: CatRiwayat,
      },
      {
        path: "riwayat-cat/:slug/hasil",
        name: "member-riwayat-cat-hasil",
        component: TryoutHasil,
      },
      {
        path: "peringkat-cat",
        name: "member-peringkat-cat",
        component: CatPeringkat,
      },
      {
        path: "kelas-private",
        name: "member-kelas-private",
        component: KelasPrivate,
      },
      {
        path: "kelas-private/:slug",
        name: "member-kelas-private-respon",
        component: ResponChat,
      },
      {
        path: "kelas-webinar",
        name: "member-kelas-webinar",
        component: KelasWebinar,
      },
      {
        path: "form-komplain",
        name: "member-form-komplain",
        component: FormKomplain,
      },
      {
        path: "form-komplain/:slug",
        name: "member-form-komplain-respon",
        component: ResponChat,
      },
      {
        path: "cek-garansi",
        name: "member-cek-garansi",
        component: CekGaransi,
      },
      {
        path: "in-progress",
        name: "member-tutorial",
        component: InProgressPage,
      },
      {
        path: "upgrade-akun",
        name: "member-upgrade-akun",
        component: UpgradeAkun,
      },
      {
        path: "tutorial",
        name: "member-tutorial",
        component: Tutorial,
      },
      {
        path: "dashboard-admin",
        name: "member-dashboard-admin",
        component: DashboardAdmin,
      },
      {
        path: "data-user",
        name: "member-data-user",
        component: DataUser,
      },
      {
        path: "bonus-materi",
        name: "member-bonus-materi",
        component: BonusMateri,
      },
      {
        path: "paket-soal",
        name: "member-paket-soal",
        component: PaketSoal,
      },
      {
        path: "bank-soal",
        name: "member-bank-soal",
        component: BankSoal,
      },
    ],
    beforeEnter: (
      to: RouteLocationNormalized,
      from: RouteLocationNormalized,
      next: any
    ) => {
      // console.log(
      //   "$gf.getRole() ",
      //   $gf.getRole(),
      //   $gf.getRole() === "member"
      // );
      if ($ck.isCookieAvailable("login-data") && $gf.getRole() === "member") {
        if ( $gf.getRole() === "member" ) {
          next();
        } else {
          next({ name: "login" });
        }
      } else {
        next({ name: "login" });
      }
    },
  },
  {
    path: "/admin",
    component: SideMenu,
    children: [
      {
        path: "",
        name: "admin-dashboard",
        component: DashboardAdmin,
      },
      {
        path: "materi-penunjang",
        name: "admin-materi-penunjang",
        component: MateriPenunjang,
      },
      {
        path: "materi-penunjang/:slug",
        name: "admin-detail-materi-penunjang",
        component: DetailMateri,
      },
      {
        path: "tips-and-trik",
        name: "admin-tips-and-trik",
        component: TipsAndTrik,
      },
      {
        path: "tips-and-trik/:slug",
        name: "admin-detail-tips-and-trik",
        component: DetailMateri,
      },
      {
        path: "tryout-skb",
        name: "admin-tryout-skb",
        component: TryoutSkb,
      },
      {
        path: "tryout-skb/:slug",
        name: "admin-tryout-skb-exam",
        component: TryoutExam,
      },
      {
        path: "tryout-skd",
        name: "admin-tryout-skd",
        component: TryoutSkd,
      },
      {
        path: "tryout-skd/:slug",
        name: "admin-tryout-skd-exam",
        component: TryoutExam,
      },
      // {
      //   path: "cat-pppk",
      //   name: "admin-cat-pppk",
      //   component: CatPppk,
      // },
      // {
      //   path: "cat-pppk/:slug",
      //   name: "admin-cat-pppk-exam",
      //   component: TryoutExam,
      // },
      {
        path: "tryout-hasil/:slug",
        name: "admin-tryout-hasil",
        component: TryoutHasil,
      },
      {
        path: "riwayat-cat",
        name: "admin-riwayat-cat",
        component: CatRiwayat,
      },
      {
        path: "peringkat-cat",
        name: "admin-peringkat-cat",
        component: CatPeringkat,
      },
      {
        path: "kelas-private",
        name: "admin-kelas-private",
        component: KelasPrivate,
      },
      {
        path: "kelas-private/:slug",
        name: "admin-kelas-private-respon",
        component: ResponChat,
      },
      {
        path: "kelas-webinar",
        name: "admin-kelas-webinar",
        component: KelasWebinar,
      },
      {
        path: "form-komplain",
        name: "admin-form-komplain",
        component: FormKomplain,
      },
      {
        path: "form-komplain/:slug",
        name: "admin-form-komplain-respon",
        component: ResponChat,
      },
      {
        path: "cek-garansi",
        name: "admin-cek-garansi",
        component: CekGaransi,
      },
      {
        path: "in-progress",
        name: "admin-tutorial",
        component: InProgressPage,
      },
      {
        path: "upgrade-akun",
        name: "admin-upgrade-akun",
        component: UpgradeAkun,
      },
      {
        path: "tutorial",
        name: "admin-tutorial",
        component: Tutorial,
      },
      {
        path: "data-user",
        name: "admin-data-user",
        component: DataUser,
      },
      {
        path: "bonus-materi",
        name: "admin-bonus-materi",
        component: BonusMateri,
      },
      {
        path: "paket-soal",
        name: "admin-paket-soal",
        component: PaketSoal,
      },
      {
        path: "bank-soal",
        name: "admin-bank-soal",
        component: BankSoal,
      },
    ],
    beforeEnter: (
      to: RouteLocationNormalized,
      from: RouteLocationNormalized,
      next: any
    ) => {
      // console.log(
      //   "$gf.getRole() ",
      //   $gf.getRole(),
      //   $gf.getRole() === "admin"
      // );
      if ($ck.isCookieAvailable("login-data")) {
        if ( $gf.getRole() === "admin" ) {
          next();
        } else {
          next({ name: "login" });
        }
      } else {
        next({ name: "login" });
      }
    },
  },
  {
    path: "/login",
    name: "login",
    component: Login,
    beforeEnter: (
      to: RouteLocationNormalized,
      from: RouteLocationNormalized,
      next: any
    ) => {
      // console.log(
      //   "$gf.getRole() ",
      //   $gf.getRole(),
      //   $gf.getRole() === "admin"
      // );
      if ($ck.isCookieAvailable("login-data") === false) {
        next();
      } else {
        router.push('/'+ $gf.getRole());
      }
    },
  },
  {
    path: "/register",
    name: "register",
    component: Register,
    beforeEnter: (
      to: RouteLocationNormalized,
      from: RouteLocationNormalized,
      next: any
    ) => {
      // console.log(
      //   "$gf.getRole() ",
      //   $gf.getRole(),
      //   $gf.getRole() === "admin"
      // );
      if ($ck.isCookieAvailable("login-data") === false) {
        next();
      } else {
        router.push('/'+ $gf.getRole());
      }
    },
  },
  {
    path: "/:pathMatch(.*)*",
    component: ErrorPage,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    return savedPosition || { left: 0, top: 0 };
  },
});

export default router;
