<template>
  <div class="grid grid-cols-12 gap-3 sm:gap-6">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-col md:flex-row items-center h-auto md:h-10">
          <h2 class="text-lg font-medium truncate mr-5">Riwayat CAT</h2>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="col-span-12 lg:col-span-12 xxl:col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in dataRiwayat.data"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.kode }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.type }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.totalSkor }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.status }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.startDate }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.endDate }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Detail "
                    >
                      <FileIcon
                        class="w-6 h-6 cursor-pointer hover:text-theme-1"
                        @click="goDetail(u)"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <LoadingDiv id="loadingCR" />
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li
            v-if="dataRiwayat.page !== 1"
            @click="goToPage(1)"
          >
            <span class="pagination__link">
              <ChevronsLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataRiwayat.page !== 1"
            @click="beforePage()"
          >
            <span class="pagination__link">
              <ChevronLeftIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-for="(p, idx) in dataRiwayat.paginate.pages"
            :key="idx"
          >
            <span
              :class="['pagination__link cursor-pointer', (dataRiwayat.page === p ? 'pagination__link--active' : '')]"
              @click="goToPage(p)"
            >{{ p }}</span>
          </li>
          <li
            v-if="dataRiwayat.page !== dataRiwayat.paginate.totalPages"
            @click="nextPage()"
          >
            <span class="pagination__link">
              <ChevronRightIcon class="w-4 h-4" />
            </span>
          </li>
          <li
            v-if="dataRiwayat.page !== dataRiwayat.paginate.totalPages"
            @click="lastPage()"
          >
            <span class="pagination__link">
              <ChevronsRightIcon class="w-4 h-4" />
            </span>
          </li>
        </ul>
        <select
          class="w-20 input box mt-3 sm:mt-0"
          v-model="dataRiwayat.pageSize"
          v-on:change="changePS();"
        >
          <option>5</option>
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, provide, reactive } from "vue";
import { Files, filesPPPK } from "./cat-riwayat";
import { useRouter } from "vue-router";
import { gf as $gf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();
    const router = useRouter();
    const config = $gf.configGet();

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };
    const tryoutHasil = (data: any): void => {
      router.push({name: 'tryout-hasil', params: data});
    }

    const dataRiwayat = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    })

    const titleTable = [
      "Kode",
      "Type",
      "Total Skor",
      "Status",
      "Start Date",
      "End Date",
      "#"
    ];

    const getData = () => {
      cash('#loadingCR').show();

      const page = dataRiwayat.page;
      const pageSize = dataRiwayat.pageSize;
      axios
        .get(api.getHistoryCat + '?page=' + page + '&pageSize=' + pageSize, config)
        .then(async (result) => {
          const ds = result.data.diagnostic;
          const rs = result.data.response;
          console.log('response get hc ', rs);

          dataRiwayat.data = rs;
          dataRiwayat.paginate  = $gf.paginate(ds.totalData, page, pageSize, 3);
          console.log('dataRiwayat.paginate ', dataRiwayat.paginate);
          cash('#loadingCR').hide();
        })
        .catch((error) => {
          console.log("response get hc err ", error.response);
          $gf.handleJwtExpired(error);
        });
    };
    getData();

    const changePS = () => {
      getData();
    }

    const goToPage = (p: number) => {
      dataRiwayat.page = p;
      getData();
    }

    const nextPage = () => {
      dataRiwayat.page = dataRiwayat.page + 1;
      getData();
    }

    const beforePage = () => {
      dataRiwayat.page = dataRiwayat.page - 1;
      getData();
    }

    const lastPage = () => {
      dataRiwayat.page = dataRiwayat.paginate.totalPages;
      getData();
    }

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
      tryoutHasil,
      titleTable,
      dataRiwayat,
      router,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
    };
  },
  data() {
    return {
      filter: {
        field: "skd",
      }
    }
  },
  methods: {
    goDetail(data: any): void{
      this.router.push('riwayat-cat/' + data.type + '/hasil' + '?kode=' + data.kode);
    }
  }
});
</script>
