
import { defineComponent, reactive } from 'vue';
import { useRoute } from "vue-router";
import { gf as $gf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const route = useRoute();
    const slug: any = route.params.slug;
    const detail = reactive({
      data: {},
      embed: "",
    })

    const urlSlug = slug.slice(4, slug.length);
    const type = slug.slice(0, 3) === 'vdo' ? 'video' : 'pdf';
    console.log('type and slug ', type , slug);

    const config = $gf.configGet();
    axios
      .get(api.getMateri + `${type}/${urlSlug}`, config)
      .then(async (result) => {
        const rs = result.data.response;
        console.log('response detail mp ', rs);
        detail.data = rs;
        if (type === 'video') {
          detail.embed = $gf.getParameterByName('v');
        }
      })
      .catch((error) => {
        console.log("response detail mp err ", error.response);
        $gf.handleJwtExpired(error);
      });

    return {
      slug,
      type,
      detail,
    }
  }
})
