<template>
  <div class="grid grid-cols-12 gap-3 md:gap-4">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex flex-row items-center h-auto sm:h-10">
          <h2 class="text-xl font-medium truncate">Bonus Materi</h2>
          <div class="ml-auto mt-2 sm:mt-0 flex flex-row items-center">
            <button
              class="button bg-theme-1 hover:bg-primary text-white flex items-center w-auto mr-2"
              @click="$gf.showModal('#modal-data-user')"
            >
              <PlusIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Tambah</span>
            </button>
            <button class="button bg-theme-1 hover:bg-primary text-white flex">
              <RefreshCcwIcon class="w-4 h-4 mr-0 md:mr-2" />
              <span class="hidden md:block">Reload Data</span>
            </button>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="intro-y col-span-12">
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y col-span-12 box pt-3 px-3 pb-5">
        <div class="grid grid-cols-3 gap-2 mb-3">
          <div class="w-full col-span-3 md:col-span-3">
            <div
              class="w-full md:w-56 relative text-gray-700 dark:text-gray-300 md:ml-auto"
            >
              <input
                type="text"
                class="input w-full box pr-10 border border-gray-400 dark:bg-dark-4"
                placeholder="Search..."
              />
              <SearchIcon
                class="w-4 h-4 absolute my-auto inset-y-0 mr-3 right-0"
              />
            </div>
          </div>
        </div>
        <div class="overflow-x-auto scrollbar-nav-soal">
          <table class="table">
            <thead>
              <tr>
                <th
                  v-for="(k, idx) in titleTable"
                  v-bind:key="idx"
                  class="border-b-2 dark:border-dark-5 whitespace-nowrap"
                >
                  {{ k }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(u, idx) in paginate.dataPaginate"
                v-bind:key="idx"
                :class="[idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '']"
              >
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.peringkat }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.nomorTes }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.namaPeserta }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.totalSkor }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap">
                  {{ u.status }}
                </td>
                <td class="border-b dark:border-dark-5 whitespace-nowrap w-20">
                  <div class="flex flex-row">
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block mr-2"
                      content="Edit"
                    >
                      <Edit3Icon
                        class="w-6 h-6 cursor-pointer hover:text-theme-1"
                        @click="editUser()"
                      />
                    </Tippy>
                    <Tippy
                      tag="a"
                      href="javascript:;"
                      class="inline-block"
                      content="Hapus"
                    >
                      <Trash2Icon
                        class="w-6 h-6 cursor-pointer hover:text-theme-1"
                        @click="$gf.showModal('#delete-modal-data-user')"
                      />
                    </Tippy>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li>
            <a
              class="pagination__link"
              href="javascript:;"
              @click="firstPage(paginate.startIndex)"
            >
              <ChevronsLeftIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a class="pagination__link" href="javascript:;" @click="backPage()">
              <ChevronLeftIcon class="w-4 h-4" />
            </a>
          </li>
          <li v-for="(ku, idx) in paginate.pages" v-bind:key="idx">
            <a
              :class="[
                'pagination__link',
                ku === paginate.currentPage ? 'pagination__link--active' : '',
              ]"
              href="#"
            >
              {{ ku }}
            </a>
          </li>
          <li>
            <a class="pagination__link" href="javascript:;" @click="nextPage()">
              <ChevronRightIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a
              class="pagination__link"
              href="javascript:;"
              @click="lastPage(paginate.endIndex)"
            >
              <ChevronsRightIcon class="w-4 h-4" />
            </a>
          </li>
        </ul>
        <select class="w-20 input box mt-3 sm:mt-0">
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
    <div id="modal-data-user" class="modal p-0">
      <div class="modal__content modal__content--lg">
        <div
          class="flex items-center px-5 py-5 sm:py-3 border-b border-gray-200 dark:border-dark-5"
        >
          <h2 class="font-medium text-base mr-auto">
            {{ section === "add" ? "Tambah Bonus Materi" : "Edit Bonus Materi" }}
          </h2>
          <a
            href="javascript:;"
            class="right-0 top-0"
            @click="$gf.hideModal('#modal-data-user')"
          >
            <xIcon class="w-6 h-6 text-gray-500" />
          </a>
        </div>
        <div class="px-3 md:px-5 py-3">
          <div class="flex flex-col sm:flex-row items-center">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Judul</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Judul Materi"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Jenis</label>
            <div class="w-full md:w-5/6 flex flex-row">
              <TailSelect
                v-model="select"
                :options="{
                  classNames: 'w-full',
                }"
              >
                <option
                  v-for="(skd, idx) in fSKD"
                  v-bind:key="idx"
                  :value="skd.slug"
                >
                  {{ skd.fileName }}
                </option>
              </TailSelect>
            </div>
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Embed</label>
            <textarea
              class="input w-full min-h-full border flex-1"
              placeholder="Kode Embed Google Drive"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Lampiran</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Lampiran"
            />
          </div>
          <div class="flex flex-col sm:flex-row items-center mt-3">
            <label class="w-full sm:w-20 sm:text-right sm:mr-5">Banner</label>
            <input
              type="text"
              class="input w-full border flex-1"
              placeholder="Banner"
            />
          </div>
        </div>
        <div
          class="px-5 py-3 text-center border-t border-gray-200 dark:border-dark-5"
        >
          <button
            type="button"
            class="button w-20 bg-theme-6 text-white mr-2"
            @click="$gf.hideModal('#modal-data-user')"
          >
            Close
          </button>
          <button
            type="button"
            class="button w-20 bg-theme-1 text-white"
            @click="$gf.hideModal('#modal-data-user')"
          >
            Simpan
          </button>
        </div>
      </div>
    </div>
    <div id="delete-modal-data-user" class="modal">
      <div class="modal__content">
        <div class="p-5 text-center">
          <xCircleIcon class="w-16 h-16 text-theme-6 mx-auto mt-3" />
          <div class="text-3xl mt-5">Are you sure?</div>
          <div class="text-gray-600 mt-2">
            Do you really want to delete these records? This process cannot be
            undone.
          </div>
        </div>
        <div class="px-5 pb-8 text-center">
          <button
            type="button"
            data-dismiss="modal"
            class="button w-24 border text-gray-700 dark:border-dark-5 dark:text-gray-300 mr-1"
            @click="$gf.hideModal('#delete-modal-data-user')"
          >
            Cancel
          </button>
          <button
            type="button"
            class="button w-24 bg-theme-6 text-white"
            @click="$gf.hideModal('#delete-modal-data-user')"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, provide } from "vue";
import { Files, pplist, filesPPPK, filesSKD, setDataList } from "./bonus-materi";
import { useRouter } from "vue-router";
import { helper as $h } from "../../utils/helper";

export default defineComponent({
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();
    const router = useRouter();
    let files: Array<Files> = [];
    let fSKD: Array<Files> = [];
    let dataTPPPK: any;

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };

    const saveUrl = (data: any) => {
      dataTPPPK = data;
      console.log("dataTPPPK", dataTPPPK);
      cash("#modal-data-user").modal("show");
    };
    const gotoTryoutExam = () => {
      console.log("gotoTryoutExam ", dataTPPPK);
      cash("#modal-data-user").modal("hide");
      router.push({ name: "data-user-exam", params: dataTPPPK });
    };

    const getData = () => {
      files = filesPPPK();
      fSKD = filesSKD();
    };
    getData();

    const titleTable: Array<string> = [
      "Banner",
      "Judul",
      "Jenis",
      "Embed",
      "Lampiran",
      "#",
    ];

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
      files,
      saveUrl,
      gotoTryoutExam,
      fSKD,
      titleTable,
    };
  },
  data() {
    return {
      select: "",
      dataList: [] as pplist[],

      // pagination
      minIndex: 0,
      maxPerPage: 5,
      maxIndex: 4,
      activePage: 0,
      numberOfPages: [],
      goToPageInput: 0,
      sortedData: [],
      searchActive: 0,
      formData: {},

      // another pagination
      paginate: {},
      currentPage: 1,
      pageSize: 10,
      maxPages: 3,

      // for modal data user
      section: "add",
    };
  },
  methods: {
    init() {
      this.dataList = setDataList();
      console.log("this.dataList ", this.dataList);
      this.getDataPaginate();
    },
    // START FUNCTION-PAGINATION-TABLE
    pagesCounter(itemLength: number) {
      let m = Math.ceil(itemLength / this.maxPerPage);
      if (m <= 1) {
        m = 1;
      }
      const nop: any = Array(m)
        .fill(0, 0)
        .map((x, i) => i);
      this.numberOfPages = nop;
    },
    openPage(selectedPage: number) {
      const page = selectedPage + 1;
      this.maxIndex = page * this.maxPerPage - 1;
      this.minIndex = page * this.maxPerPage - this.maxPerPage;
      this.activePage = selectedPage;
    },
    changeMaxPerPage(e: number) {
      const page = 1;
      this.maxPerPage = e;
      this.pagesCounter(this.dataList.length);
      this.maxIndex = page * this.maxPerPage - 1;
      this.minIndex = page * this.maxPerPage - this.maxPerPage;
      this.activePage = 0;
    },
    goToPage() {
      const page = this.goToPageInput - 1;
      if (
        typeof page === "number" &&
        page <= this.numberOfPages.length - 1 &&
        page >= 0
      ) {
        this.openPage(page);
      }
    },
    // END FUNCTION-PAGINATION-TABLE

    getData(currentPage: number, pageSize: number, maxPages: number) {
      return $h.paginate(
        this.dataList.length,
        currentPage,
        pageSize,
        maxPages,
        this.dataList
      );
    },
    getDataPaginate() {
      const dddlist = this.getData(
        this.currentPage,
        this.pageSize,
        this.maxPages
      );
      this.paginate = dddlist;
    },
    nextPage() {
      this.currentPage++;
      const dddlist = this.getData(
        this.currentPage,
        this.pageSize,
        this.maxPages
      );
      this.paginate = dddlist;
    },
    backPage() {
      this.currentPage--;
      const dddlist = this.getData(
        this.currentPage,
        this.pageSize,
        this.maxPages
      );
      this.paginate = dddlist;
    },
    lastPage(endIndex: number) {
      const dddlist = this.getData(endIndex + 1, this.pageSize, this.maxPages);
      this.paginate = dddlist;
    },
    firstPage(startIndex: number) {
      const dddlist = this.getData(
        startIndex + 1,
        this.pageSize,
        this.maxPages
      );
      this.paginate = dddlist;
    },

    addUser() {
      this.section = "add";
      cash("#modal-data-user").modal("show");
    },
    editUser() {
      this.section = "edit";
      cash("#modal-data-user").modal("show");
    },
  },
  mounted() {
    // console.log('paginate data ', $h.paginate(setDataList().length, 1, 10, 3, setDataList()));
    this.init();
  },
});
</script>
