
import { defineComponent, ref, provide } from "vue";
import { Files, filesSKD } from "./tryout-skd";
import { useRouter } from "vue-router";

export default defineComponent({
  components: {},
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();
    const router = useRouter();
    let files: Array<Files> = [];
    let dataTSKD: any;

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };

    const saveUrl = (data: any) => {
      dataTSKD = data;
      console.log('dataTSKD', dataTSKD);
      cash("#modal-tryout-skd").modal("show");
    }
    const gotoTryoutExam = () => {
      console.log('gotoTryoutExam ', dataTSKD);
      cash("#modal-tryout-skd").modal("hide");
      router.push({name: 'tryout-skd-exam', params: dataTSKD});
    }

    const getData = () => {
      files = filesSKD();
    };
    getData();

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
      files,
      saveUrl,
      gotoTryoutExam,
    };
  },
});
