<template>
  <div>
    <div class="flex items-center mt-8">
      <h2 class="intro-y text-lg font-medium mr-auto">Materi {{ detail.data.title }}</h2>
      <a
        class="button w-auto mr-2 mb-2 flex items-center justify-center bg-theme-1 text-white"
        :href="detail.data.content"
        target="_blank"
        v-if="type === 'pdf'"
      >
        <DownloadCloudIcon class="w-6 h-6 md:mr-2 mr-0" />
        <span class="hidden md:block">Download Document</span>
      </a>
    </div>
    <!-- BEGIN: Wizard Layout -->
    <div
      class="intro-y box py-5 px-5 md:py-10 md:px-5 mt-5 w-auto h-full max-h-screen"
      style="height: 500px"
      v-if="type === 'pdf'"
    >
      <h2 class="intro-y text-lg text-center font-medium mr-auto mb-5">
        {{ detail.data.slug }}
      </h2>
      <iframe
        class="md:w-5/6 m-auto h-auto md:h-5/6 max-w-full w-full" style="height: 50vh;"
        :src="'https://drive.google.com/viewerng/viewer?url='+ detail.data.content +'?pid=explorer&amp;efh=false&amp;a=v&amp;chrome=false&amp;embedded=true'"
        embedded="true"
      ></iframe>
    </div>
    <div
      class="intro-y box py-5 px-5 md:py-10 md:px-5 mt-5 w-auto h-full max-h-screen"
      style="height: 500px"
      v-else
    >
      <h2 class="intro-y text-lg text-center font-medium mr-auto mb-5">
        {{ detail.data.slug }}
      </h2>
      <iframe
        class="md:w-5/6 m-auto h-auto md:h-5/6 max-w-full w-full"
        :src="'https://www.youtube.com/embed/' + detail.embed"
        frameborder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowfullscreen=""
      ></iframe>
    </div>
    <!-- END: Wizard Layout -->
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from 'vue';
import { useRoute } from "vue-router";
import { gf as $gf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  setup() {
    const route = useRoute();
    const slug: any = route.params.slug;
    const detail = reactive({
      data: {},
      embed: "",
    })

    const urlSlug = slug.slice(4, slug.length);
    const type = slug.slice(0, 3) === 'vdo' ? 'video' : 'pdf';
    console.log('type and slug ', type , slug);

    const config = $gf.configGet();
    axios
      .get(api.getMateri + `${type}/${urlSlug}`, config)
      .then(async (result) => {
        const rs = result.data.response;
        console.log('response detail mp ', rs);
        detail.data = rs;
        if (type === 'video') {
          detail.embed = $gf.getParameterByName('v');
        }
      })
      .catch((error) => {
        console.log("response detail mp err ", error.response);
        $gf.handleJwtExpired(error);
      });

    return {
      slug,
      type,
      detail,
    }
  }
})
</script>
