const filesPaketSKD = (): Array<Files> => {
  return paketSKD.map((e, idx) => {
    const obj = {
      fileName: `${e.fileName} ${idx+1}`,
      slug: `${e.slug}-${idx+1}`,
    };
    return obj;
  });
};

