const getRandomInt = (min: number, max: number) => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

type pplist = {
  peringkat: number;
  nomorTes: number;
  namaPeserta: string;
  skorTWK: number;
  skorTIU: number;
  skorTKP: number;
  totalSkor: number;
  status: string;
};

const dataList: pplist[] = [];
const setDataList = (): pplist[] => {
  for (let i = 0; i < 100; i++) {
    const twk: number = getRandomInt(1, 200);
    const tiu: number = getRandomInt(1, 100);
    const tkp: number = getRandomInt(1, 200);
    const total: number = twk + tiu + tkp;

    const obj = {
      peringkat: getRandomInt(1, 1000),
      nomorTes: getRandomInt(1, 10000),
      namaPeserta: "Test " + getRandomInt(1, 1000),
      skorTWK: twk,
      skorTIU: tiu,
      skorTKP: tkp,
      totalSkor: total,
      status: total > 350 ? "Lulus" : "Gagal",
    };
    dataList.push(obj);
  }

  return dataList;
};

export { Files, pplist, filesPaketSKD, filesSKD, setDataList };
