<template>
  <div class="grid grid-cols-12 gap-6">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-6">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mr-5">Materi Penunjang</h2>
          <a href="" class="ml-auto flex text-theme-1 dark:text-theme-10">
            <RefreshCcwIcon class="w-4 h-4 mr-3" /> Reload Data
          </a>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
    <div class="col-span-12 lg:col-span-12 xxl:col-span-12">
      <!-- BEGIN: File Manager Filter -->
      <div class="intro-y flex flex-col-reverse sm:flex-row items-center">
        <div class="w-full sm:w-auto relative ml-auto sm:mt-0">
          <SearchIcon
            class="w-4 h-4 absolute my-auto inset-y-0 ml-3 left-0 z-10 text-gray-700 dark:text-gray-300"
          />
          <input
            type="text"
            class="input w-full sm:w-80 box pl-10 text-gray-700 dark:text-gray-300"
            placeholder="Search files"
          />
        </div>
      </div>
      <!-- END: File Manager Filter -->
      <!-- BEGIN: Directory & Files -->
      <div class="intro-y grid grid-cols-12 gap-3 sm:gap-6 mt-5">
        <div
          v-for="(faker, fakerKey) in listMP.list"
          :key="fakerKey"
          class="intro-y col-span-6 sm:col-span-4 md:col-span-3 xxl:col-span-2"
        >
          <div
            class="file box rounded-md pt-8 pb-5 px-3 sm:px-5 relative zoom-in"
            @click="setMPLS(faker);"
          >
            <UrlSlug
              :href="`materi-penunjang/pdf-${faker.slug}`"
              class="w-3/5 file__icon file__icon--file mx-auto"
              @click="setMPLS(faker);"
            >
              <div class="file__icon__file-name">
                {{ faker.type }}
              </div>
            </UrlSlug>
            <UrlSlug
              :href="`materi-penunjang/pdf-${faker.slug}`"
              class="block font-medium mt-4 text-center truncate"
              @click="setMPLS(faker);"
            >
              {{ faker.title }}
            </UrlSlug>
            <!-- <div class="text-gray-600 text-xs text-center mt-0.5">
              {{ faker.size }}
            </div> -->
          </div>
        </div>
      </div>
      <LoadingDiv id="loadingMP" />
      <!-- END: Directory & Files -->
      <!-- BEGIN: Pagination -->
      <div
        class="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-6"
      >
        <ul class="pagination">
          <li>
            <a class="pagination__link" href="">
              <ChevronsLeftIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a class="pagination__link" href="">
              <ChevronLeftIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a class="pagination__link" href="">1</a>
          </li>
          <li>
            <a class="pagination__link pagination__link--active" href="">2</a>
          </li>
          <li>
            <a class="pagination__link" href="">3</a>
          </li>
          <li>
            <a class="pagination__link" href="">
              <ChevronRightIcon class="w-4 h-4" />
            </a>
          </li>
          <li>
            <a class="pagination__link" href="">
              <ChevronsRightIcon class="w-4 h-4" />
            </a>
          </li>
        </ul>
        <select class="w-20 input box mt-3 sm:mt-0">
          <option>10</option>
          <option>25</option>
          <option>35</option>
          <option>50</option>
        </select>
      </div>
      <!-- END: Pagination -->
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, provide, reactive } from "vue";
import { Files, filesMP } from "./materi-penunjang";
// import { helper as $h } from "@/utils/helper";
import { gf as $gf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import axios from "axios";

export default defineComponent({
  components: {},
  setup() {
    const salesReportFilter = ref("");
    const importantNotesRef = ref<HTMLElement>();
    let files: Array<Files> = [];

    provide("bind[importantNotesRef]", (el: HTMLElement) => {
      importantNotesRef.value = el;
    });

    const setFiles = () => {
      files = filesMP();
    };
    const prevImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("prev");
    };
    const nextImportantNotes = () => {
      const el: any = importantNotesRef.value;
      el.tns.goTo("next");
    };
    const setMPLS = (data: Files) => {
      window.localStorage.setItem(`${data.type}-${data.slug}`, JSON.stringify(data));
    };

    setFiles();
    // console.log("files ", files);
    // console.log('paginate ', $h.paginate(files.length, 5, 5, 3, files));

    const listMP = reactive({
      list: [],
    });
    const config = $gf.configGet();
    axios
      .get(api.getMateri + 'pdf', config)
      .then(async (result) => {
        const rs = result.data.response;
        console.log('response get mp ', rs);
        listMP.list = rs;
        cash("#loadingMP").hide();
      })
      .catch((error) => {
        console.log("response get mp err ", error.response);
        $gf.handleJwtExpired(error);
      });

    const totalItems = 3;
    const currentPage = 1;
    const pageSize = 10;
    const maxPages = 3;

    const paginate = $gf.paginate(totalItems, currentPage, pageSize, maxPages);
    console.log('paginate ', paginate);

    return {
      salesReportFilter,
      prevImportantNotes,
      nextImportantNotes,
      files,
      setMPLS,
      listMP,
    };
  },
});
</script>
